<template>
  <div class="col-md-6 col-lg-4">
    <div class="card mb-4">
      <img
        class="card-img-top"
        :src="restaurant.image"
        alt="Card image cap"
        width="286px"
        height="180px"
      />
      <div class="card-body">
        <p class="card-text title-wrap">
          <router-link
            :to="{ name: 'restaurant', params: { id: restaurant.id } }"
          >
            {{ restaurant.name }}
          </router-link>
        </p>
        <span class="badge badge-secondary">{{
          restaurant.Category.name
        }}</span>
        <p class="card-text text-truncate">
          {{ restaurant.description }}
        </p>
      </div>
      <div class="card-footer">
        <button
          v-if="restaurant.isFavorited"
          @click.stop.prevent="deleteFavorite(restaurant.id)"
          type="button"
          class="btn btn-danger btn-border favorite mr-2"
        >
          移除最愛
        </button>
        <button
          v-else
          @click.stop.prevent="addFavorite(restaurant.id)"
          type="button"
          class="btn btn-primary btn-border favorite mr-2"
        >
          加到最愛
        </button>
        <button
          v-if="restaurant.isLiked"
          @click.stop.prevent="deleteLike(restaurant.id)"
          type="button"
          class="btn btn-danger like mr-2"
        >
          Unlike
        </button>
        <button
          v-else
          @click.stop.prevent="addLike(restaurant.id)"
          type="button"
          class="btn btn-primary like mr-2"
        >
          Like
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "./../utilities/helpers";
import userAPI from "./../apis/users";

export default {
  props: {
    initialRestaurant: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      restaurant: this.initialRestaurant,
    };
  },
  methods: {
    async addFavorite(restaurantId) {
      try {
        const { data } = await userAPI.addFavorite({ restaurantId });
        if (data.status !== "success") {
          throw new Error(data.message);
        }
        this.restaurant = {
          ...this.restaurant,
          isFavorited: true,
        };
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "無法將餐廳加入我的最愛，請稍候！",
        });
        console.log(error);
      }
    },
    async deleteFavorite (restaurantId) {
      try {
        const {data} = await userAPI.deleteFavorite({restaurantId})
        if (data.status !== 'success') {
          throw new Error (data.message)
        }
        this.restaurant = {
          ...this.restaurant,
          isFavorited: false,
        };
      } catch (error) {
        Toast.fire({
          icon: 'error',
          title: '無法將餐廳從我的最愛移除，請稍候！'
        })
        console.log(error);
      }
    },
    async addLike (restaurantId) {
      try{
        const {data} = await userAPI.addLiked({restaurantId})
        console.log(data)
        if (data.status !== 'success') {
          throw new Error(data.message)
        }
        this.restaurant = {
          ...this.restaurant,
          isLiked: true,
        }
      } catch (error) {
        Toast.fire ({
          icon: 'error',
          title: '無法成功按讚，請稍後再試！'
        })
        console.log(error)
      }
    },
    async deleteLike (restaurantId) {
      try {
        const {data} = await userAPI.deleteLiked({restaurantId})
        
        if (data.status !== 'success') {
          throw new Error (data.message)
        }

        this.restaurant = {
          ...this.restaurant,
          isLiked: false,
        }
      } catch (error) {
        Toast.fire ({
          icon: 'error',
          title: '無法成功移除讚，請稍候再試！'
        })
        console.log(error)
      }
    }
  },
};
</script>
